body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-plus-jakarta-sans: "Plus Jakarta Sans";

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;
  --font-size-21xl: 40px;
  --font-size-mini: 15px;
  --font-size-3xl: 22px;

  /* Colors */
  --color-white: #fff;
  --color-cornflowerblue: #2196f3;
  --color-black: #000;
  --color-darkslategray: #434e58;
  --color-slategray: #78828a;
  --color-dimgray: #6c6c6c;
  --color-gray: rgba(0, 0, 0, 0.5);

  /* Gaps */
  --gap-xl: 20px;
  --gap-11xl: 30px;
  --gap-281xl: 300px;
  --gap-8xl: 27px;
  --gap-4xl: 23px;
  --gap-3xl: 22px;
  --gap-7xl: 26px;
  --gap-190xl: 209px;
  --gap-4xs: 9px;
  --gap-2xs: 11px;
  --gap-41xl: 60px;
  --gap-26xl: 45px;
  --gap-9xs: 4px;

  /* Paddings */
  --padding-31xl: 50px;
  --padding-11xl: 30px;
  --padding-xl: 20px;
  --padding-3xs: 10px;
  --padding-7xs: 6px;
  --padding-8xs: 5px;
  --padding-mid: 17px;

  /* border radiuses */
  --br-11xl: 30px;
  --br-mini: 15px;
  --br-xl: 20px;
  --br-3xs: 10px;
}
